import axios from 'axios'
import { ref } from 'vue'
import Vuex from 'vuex'
const df = ref([])
const df_paired = ref([])

const df_sankeyHumana = ref([])
const df_sankeyHumanb = ref([])
const df_sankeyHu = ref([])
const df_maithu = ref([])
const df_nkthu = ref([])
const df_gemthu = ref([])
axios.get('./single_data.json').then(res => {
  df.value = res.data
})
axios.get('./paired_data.json').then(ress => {
  df_paired.value = ress.data
})
axios.get('./sankeyData/test_sankeyhua.json').then(ress => {
  df_sankeyHumana.value = ress.data
})
axios.get('./sankeyData/test_sankeyhub.json').then(ress => {
  df_sankeyHumanb.value = ress.data
})
axios.get('./sankeyData/sankeyHu.json').then(ress => {
  df_sankeyHu.value = ress.data
})
axios.get('./graphData/humanMAIT.json').then(ress => {
  df_maithu.value = ress.data
})
axios.get('./graphData/humanNKT.json').then(ress => {
  df_nkthu.value = ress.data
})
axios.get('./graphData/humanGEMT.json').then(ress => {
  df_gemthu.value = ress.data
})
const state = {
  name: df,
  name1: df_paired,
  name2: df_sankeyHumana,
  name3: df_sankeyHumanb,
  sankeyHu: df_sankeyHu,
  name4: df_maithu,
  name5: df_nkthu,
  name6: df_gemthu,
}
const mutations = {
}
const getters = {
}
export default new Vuex.Store({
  state,
  mutations,
  getters
})